import React, { useEffect } from 'react';
import { Loader } from '@googlemaps/js-api-loader';
import Typography from '@mui/material/Typography';
import Section from '@components/section';
import pinImage from '@images/pin.svg';

const Company = props => {
  useEffect(() => {
    const loader = new Loader({
      apiKey: 'AIzaSyBI_6Bn1nUDu3o677tqEmmW1KV7UhnKkCU',
      version: 'weekly'
    });

    loader.load().then(() => {
      const { google } = window;

      if (
        !document.getElementById('map') ||
        !google ||
        !google.maps ||
        !google.maps.Map
      ) {
        return;
      }

      const lat = 35.67612894074747;
      const lng = 139.71010032475465;

      const map = new window.google.maps.Map(document.getElementById('map'), {
        mapId: '13c8241e9ab7d73d',
        center: {
          lat,
          lng
        },
        zoom: 18
      });

      new google.maps.Marker({
        position: { lat, lng },
        map,
        icon: {
          url: pinImage,
          scaledSize: new google.maps.Size(60, 60)
        }
      });
    });
  }, []);

  return (
    <Section id='company'>
      <div className='bg-white'>
        <div className='row'>
          <div className='col-lg-7'>
            <table className='table'>
              <tbody>
                {props.data &&
                  props.data.map((d, i) => (
                    <tr key={i}>
                      <th scope='row'>
                        <Typography fontWeight='bold'>{d.key}</Typography>
                      </th>
                      <td>
                        {d.link ? (
                          <a
                            href={d.link}
                            target='_blank'
                            rel='noopener noreferrer'
                            style={{
                              textDecoration: 'underline'
                            }}
                          >
                            <Typography
                              display='inline'
                              dangerouslySetInnerHTML={{
                                __html: d.value
                              }}
                            />
                            <i
                              className='fas fa-external-link-alt'
                              style={{
                                marginLeft: '0.5em'
                              }}
                            />
                          </a>
                        ) : (
                          <Typography
                            dangerouslySetInnerHTML={{
                              __html: d.value
                            }}
                          />
                        )}
                      </td>
                    </tr>
                  ))}
              </tbody>
            </table>
          </div>
          <div className='col-lg-5'>
            <div
              id='map'
              className='w-100'
              style={{
                height: 380,
                filter: 'grayscale(100%)'
              }}
            />
            <div className='text-right'>
              <a
                className='mt-3 btn btn-outline-primary rounded px-4 py-2'
                href='https://goo.gl/maps/7fin8di13W8xvZy77'
                target='_blank'
                rel='noopener noreferrer'
              >
                <Typography>
                  Google Maps <i className='fas fa-map-marker-alt' />
                </Typography>
              </a>
            </div>
          </div>
        </div>
      </div>
    </Section>
  );
};

export default Company;
